@tailwind base;
@tailwind components;
@tailwind utilities;


@layer utilities {
   
.nav-line {
   
    
        display: block;
        -webkit-animation: thread2;
        -moz-animation:    thread2;
        -o-animation:      thread2;
        animation:         thread2;
        animation-duration: 0.5s;
        animation-timing-function: ease-in;
    
    }
@-webkit-keyframes thread2 {
	  0%   { width: 0px; }
	  100%{ width:100%;}
	}
	@-moz-keyframes thread2 {
	  0%   { width: 0px; }
	  100%{ width:100%;}
	}
	@-o-keyframes thread2 {
	  0%   { width: 0px; }
	  100%{ width:100%;}
	}
	@keyframes thread2 {
	  0%   { width: 0px; }
	  100%{ width:100%;}
	}

	div {
  -ms-overflow-style: none; /* Edge, Internet Explorer */
  scrollbar-width: none; /* Firefox */
  overflow-y: scroll;
}

div::-webkit-scrollbar {
  display: none; /* Chrome, Safari, Opera */
}

}
